.search {
    @apply flex items-center mobile:items-start mobile:flex-col mb-2;

    &Group {
        @apply w-full max-w-[550px] relative mobile:max-w-none;

        .searchInput {
            @apply w-full rounded-2xl mobile:rounded-lg shadow-popUp py-[22px] mobile:py-[10px] pr-[72px] mobile:pr-10 pl-6 mobile:pl-4 text-2xl mobile:text-sm focus:outline-none;
        }

        button {
            @apply absolute right-4 mobile:right-2 top-1/2 -translate-y-1/2 opacity-30 mobile:w-6;

            svg {
                @apply mobile:w-full;
            }
        }
    }

    .isFocused {
        @apply max-w-none w-full;

        .searchInput {
            @apply w-full;
        }

        button {
            @apply opacity-100;
        }
    }

    > p {
        @apply text-xl mobile:text-sm font-semibold px-10 mobile:px-0 mobile:mt-2;
    }
}
