.accordion {
    @apply flex justify-between flex-wrap m-0 p-0 mt-6 list-none mobile:mt-0;
}

.accordionItem {
    @apply border-b border-b-[#1E282D]/20 w-full max-w-[510px] mobile:max-w-none;
}

.accordionItemTitle {
    @apply w-full m-0;
}

.accordionItemBtn {
    @apply flex w-full cursor-pointer border-0 pt-4 pb-2 text-lg font-medium mobile:text-sm text-left;
}

.accordionItemBtn svg {
    @apply flex-shrink-0 ml-auto bg-no-repeat transition-transform;
}

.accordionItem.active svg {
    @apply transform -rotate-180;
}

.accordionItemContainer {
    @apply overflow-hidden transition-height duration-200 ease-in-out;
}

.accordionItemContent {
    @apply border-t-[#1E282D] pb-2 text-sm;
}
